import React, { Component } from "react";
import Axios from "axios";
import { Table, Image } from "react-bootstrap";

export default class Worldinfo extends Component {
  constructor() {
    super();
    this.state = {
      datas: [],
    };
  }

  componentDidMount() {
    Axios.get("https://corona.lmao.ninja/v2/countries").then((response) => {
      this.setState({ datas: response.data });
    });
  }
  render() {
    return (
      <Table striped bordered hover>
        <thead>
          <tr>
            <td>Country</td>
            <td>Total Cases</td>

            <td>Recovered</td>
            <td>Death</td>
          </tr>
        </thead>
        <tbody>
          {this.state.datas.map((itm, key) => {
            return (
              <tr>
                <td>
                  <Image
                    style={{ width: "54px", marginRight: "10px" }}
                    src={itm.countryInfo.flag} rounded
                  ></Image>
                  {itm.country}
                </td>
                <td>{itm.cases}</td>

                <td>{itm.recovered}</td>
                <td>{itm.deaths}</td>
              </tr>
            );
          })}
        </tbody>
      </Table>
    );
  }
}
