import React, { Component } from "react";
import { Col, Row, Accordion, Card, Table } from "react-bootstrap";
import Axios from "axios";
import "../../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../IndianStates/IndiaState.css";
export default class IndianStates extends Component {
  constructor() {
    super();
    this.state = {
      stateData: {},
    };
  }
  componentDidMount() {
    Axios.get("https://api.covid19india.org/state_district_wise.json").then(
      (response) => {
        this.setState({ stateData: response.data });
      }
    );
  }

  render() {
    let keys = Object.keys(this.state.stateData);

    return (
      <Row>
        <Col md={12}>
          <Accordion>
            {
            keys.map((itm, key) => {
              let districts = this.state.stateData[itm].districtData;
              let district_key = Object.keys(districts);
              let total_active = 0;
              let total_confirmed = 0;
              let total_recovered = 0;
              let total_death = 0;
console.log(this.state.stateData[itm]);
              let district_list = [];
              for (let x in districts) {
                total_active += districts[x].active;
                total_confirmed += districts[x].confirmed;
                total_recovered += districts[x].recovered;
                total_death += districts[x].deceased;
                let ob = districts[x];
                ob["dist_name"] = x;
                district_list.push(ob);
              }

              return (
                <Card>
                  <Accordion.Toggle as={Card.Header} eventKey={district_key}>
                  
                    <span className="dist-Name">{itm}</span>
                   
                   
                    <Table className ="tbl">
                        <thead>
                          <tr>
                          <td className="label"><span  >Cases</span></td>
                          <td className="label2"><span  >Active  </span></td>
                          <td className="label3"><span  >Recovered </span></td>
                          <td className="label4"><span  >Death </span></td>
                              </tr>
                              </thead>
                              <tbody>
                              <tr>
                              <td className="label"><span  >{total_confirmed} </span></td>
                              <td className="label2"><span  >  {total_active} </span></td>
                              <td className="label3"> <span  > {total_recovered} </span></td>
                              <td   className="label4"><span> {total_death}  </span></td>
                              </tr>

                                  </tbody>
                        
                            </Table>
                           
                  </Accordion.Toggle>
                  <Accordion.Collapse eventKey={district_key}>
                    <Card.Body>
                      <Table striped bordered hover>
                        <thead>
                          <tr>
                            <td>District</td>
                            <td>Confirmed</td>
                            <td>Active</td>
                            <td>Recovered</td>
                            <td>Death</td>
                          </tr>
                        </thead>
                        <tbody>
                          {district_list.map((itm, key) => {
                            return (
                              <tr>
                                <td>{itm.dist_name}</td>
                                <td>{itm.confirmed}</td>
                                <td>{itm.active}</td>
                                <td>{itm.recovered}</td>
                                <td>{itm.deceased}</td>
                              </tr>
                            );
                          })}
                        </tbody>
                      </Table>
                    </Card.Body>
                  </Accordion.Collapse>
                </Card>
              );
            })}
          </Accordion>
        </Col>
      </Row>
    );
  }
}
