import React, { Component } from "react";
import { Card, Image, Row, Col } from "react-bootstrap";
import IndianStates from "../IndianStates/IndianStates";
import Axios from "axios";
import "../India/india.css";

export default class India extends Component {

    constructor(){
        super();
        this.state = {
            data:{}
        }
    }

    componentDidMount() {
        Axios.get("https://corona.lmao.ninja/v2/countries/india").then(
          (response) => {
            this.setState({ data: response.data });
          }
        );
      }
    

  render() {
    return (
      <Row>
        <Col md={12}>
          <Image src="https://www.countryflags.io/in/shiny/64.png" fluid />
          <h3>India</h3>
        </Col>
        <Col md={12}>
          <Row>
            <Col md={3}>
              <Card className="badge badge-info" style={{ width: "18rem" }}>
                <Card.Body>
                  <Card.Title className ="zoom">TOTAL CASES</Card.Title>
                  <h3>{this.state.data.cases}</h3>
                  <Card.Text>
                    
                  </Card.Text>
                </Card.Body>
              </Card>
            </Col>
            <Col md={3}>
              <Card className="badge badge-warning" style={{ width: "18rem" }}>
                <Card.Body>
                  <Card.Title>ACTIVE CASES</Card.Title>
                  <h3>{this.state.data.active}</h3>
                  <Card.Text>
                    
                  </Card.Text>
                </Card.Body>
              </Card>
            </Col>
            <Col md={3}>
              <Card className="badge badge-success" style={{ width: "18rem" }}>
                <Card.Body>
                  <Card.Title>RECOVERD CASES</Card.Title>
                  <h3>{this.state.data.recovered}</h3>
                  <Card.Text>
                    
                  </Card.Text>
                </Card.Body>
              </Card>
            </Col>
            <Col md={3}>
              <Card className="badge badge-danger" style={{ width: "18rem" }}>
                <Card.Body>
                  <Card.Title>DEATH CASES</Card.Title>
                  <h3>{this.state.data.deaths}</h3>
                  <Card.Text>
                    
                  </Card.Text>
                </Card.Body>
              </Card>
            </Col>
            <Col md={12}  className ="statedetails">
            <IndianStates/>
            </Col>
          </Row>
        </Col>
      </Row>
    );
  }
}
