import React, { Component } from "react";
import { Image,Navbar, Nav } from "react-bootstrap";

import "../Header/Header.css";
import logo from '../images/C2019.png'
export default class Header extends Component {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
      }
      handleClick() {
          alert("click")
        console.log('Click happened');
      }
  render() {
      
    return (
      <div>
        <Navbar className="navv" bg="light" expand="lg">
          <Navbar.Brand href="/"><Image src={logo}></Image><span className="brand">C-2019 Info</span></Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end">

              
            <Nav  >
              <Nav.Link onclick="this.blur()" href="/india"><span className="brand" >India</span></Nav.Link>
              <Nav.Link  href="/worldinfo"><span className="brand">World</span></Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </div>
    );
  }
}
